import { Component, ElementRef, ViewChild } from '@angular/core';
import { FormsModule, FormBuilder, FormGroup, ReactiveFormsModule, Validators } from '@angular/forms';
import { HttpClient, HttpHeaders } from '@angular/common/http';
import { environment } from '../../../../environments/environment';
import { CommonModule } from '@angular/common';
import jsPDF from 'jspdf';
declare var bootstrap: any;
@Component({
  selector: 'app-my-transaction',
  standalone: true,
  imports: [CommonModule, FormsModule, ReactiveFormsModule],
  templateUrl: './my-transaction.component.html',
  styleUrl: './my-transaction.component.scss'
})
export class MyTransactionComponent {
  users: any[] = [];
  searchText: string = '';
  pageSize: number = 5;
  currentPage: number = 1;
  previewUrl: string | ArrayBuffer | null = null;
  toastMessage: string = '';
  newsForm: FormGroup;
  base64Image: string | null = null;

  pagination = {
    total: 0,
    page: 1,
    limit: this.pageSize
  };

  @ViewChild('errorToast', { static: false }) errorToast!: ElementRef;

  constructor(private fb: FormBuilder, private http: HttpClient) {
    this.newsForm = this.fb.group({
      title: ['', [Validators.required]],        // required
      newsLink: ['', [Validators.required]],     // required
      description: ['', [Validators.required]]   // required
    });
  }

  ngOnInit(): void {
    this.fetchtransactions();
  }

  // ✅ Fetch news with pagination
  fetchtransactions(page: number = 1, limit: number = this.pageSize) {
    this.http.get<any>(`${environment.apiUrl}/orders?populate=userOrderDetails&populate=createdBy&page=${page}&limit=${limit}`).subscribe({
      next: (res) => {
        if (res.success && res.data?.data) {
          this.users = res.data.data;
          this.pagination = res.data.pagination;
          this.currentPage = page;
        }
      },
      error: (err) => {
        console.error('Error fetching news:', err);
        this.showErrorToast('Failed to fetch news');
      }
    });
  }

  // ✅ Pagination controls
  get totalPages(): number {
    return Math.ceil(this.pagination.total / this.pagination.limit);
  }

  changePage(page: number) {
    if (page >= 1 && page <= this.totalPages) {
      this.fetchtransactions(page, this.pageSize);
    }
  }

  goPrevious() {
    this.changePage(this.currentPage - 1);
  }

  goNext() {
    this.changePage(this.currentPage + 1);
  }

  onPageSizeChange(event: any) {
    this.pageSize = +event.target.value;
    this.currentPage = 1;
    this.fetchtransactions(this.currentPage, this.pageSize);
  }


  downloadReceipt(tx: any) {
    const doc = new jsPDF();

    // Colors & Styles
    const primaryColor = [44, 62, 80]; // Dark Gray
    const secondaryColor = [100, 100, 100]; // Light Gray
    const borderColor = [220, 220, 220];

    // Header
    doc.setFontSize(14);
    doc.setFont("helvetica", "bold");
    doc.text("Indian Administrative", 14, 15);
    doc.text("Service Central Association", 14, 22);

    // Logo Placeholder (Optional, can insert image here)
    // doc.addImage('logo.png', 'PNG', 170, 8, 25, 25);

    // Billed To Section Box
    doc.setDrawColor(borderColor[0], borderColor[1], borderColor[2]);
    doc.setLineWidth(0.2);
    doc.roundedRect(10, 30, 190, 50, 3, 3); // x,y,w,h,radius

    doc.setFontSize(11);
    doc.setFont("helvetica", "normal");
    doc.text("Billed to", 14, 38);

    doc.setFontSize(10);
    doc.text(`${tx?.createdBy?.doptOfficer?.englishName || 'N/A'}`, 14, 45);
    doc.text(`${tx?.createdBy?.email || 'N/A'}`, 14, 51);
    doc.text(`${tx?.createdBy?.mobileNumber || 'N/A'}`, 14, 57);

    // Transaction Details (Right Side inside box)
    const startX = 120;
    doc.setFontSize(10);
    doc.text(`Bank Ref ID: ${tx?.bankTxnId || 'N/A'}`, startX, 38);
    doc.text(`Payment Time: ${tx?.txnDate || 'N/A'}`, startX, 45);
    doc.text(`Payment Method: ${tx?.paymentMode || 'N/A'}`, startX, 51);
    doc.text(`Bank Name: ${tx?.bankName || 'N/A'}`, startX, 57);

    // Transaction ID
    doc.setFontSize(9);
    doc.text(`Transaction ID: ${tx?.txnId || 'N/A'}`, 14, 72);

    // Item Details Table Header
    let posY = 90;
    doc.setFillColor(245, 245, 245);
    doc.rect(10, posY, 190, 10, "F");
    doc.setFontSize(10);
    doc.setFont("helvetica", "bold");
    doc.text("ITEM DETAIL", 14, posY + 7);
    doc.text("AMOUNT", 180, posY + 7, { align: "right" });

    // Item Row
    posY += 15;
    doc.setFont("helvetica", "normal");
    doc.text("Membership Fees", 14, posY);
    doc.text(`Rs ${tx?.txnAmount || 0}`, 180, posY, { align: "right" });
    doc.setFontSize(9);
    doc.setTextColor(120);
    doc.text("Annual Membership", 14, posY + 6);

    // Total Row
    posY += 20;
    doc.setDrawColor(200, 200, 200);
    doc.line(10, posY, 200, posY);
    posY += 10;
    doc.setFont("helvetica", "bold");
    doc.setFontSize(11);
    doc.text("Total :", 150, posY);
    doc.text(`Rs ${tx?.txnAmount || 0}`, 180, posY, { align: "right" });

    // Signature/Footer Section
    posY += 100;
    doc.setFont("helvetica", "normal");
    doc.setFontSize(10);
    doc.text("Treasurer", 180, posY, { align: "right" });
    doc.setFontSize(8);
    doc.text("Indian Administrative", 180, posY + 6, { align: "right" });
    doc.text("Service Central Association", 180, posY + 12, { align: "right" });

    // Bottom Footer
    doc.setFontSize(9);
    doc.setTextColor(120);
    doc.text(
      "This is a system generated invoice, in case of any query contact us.",
      105,
      285,
      { align: "center" }
    );

    // Save File
    doc.save(`Receipt_${tx?.bankTxnId || "txn"}.pdf`);
  }


  // ✅ Toast methods
  showErrorToast(message: string): void {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-success');
    toastElement.classList.add('bg-danger');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }

  showSuccessToast(message: string) {
    this.toastMessage = message;
    const toastElement = this.errorToast.nativeElement;
    toastElement.classList.remove('bg-danger');
    toastElement.classList.add('bg-success');
    const toast = new bootstrap.Toast(toastElement);
    toast.show();
  }

}
